import styles from "./Steps.module.css"

const StepSoftware = ({setFormData , setStep , formData , title}) => {

    const handleChange = e => setFormData({...formData , software:e.target.value})
    return(
        <div className={styles.container}>
            <h2>{title}</h2>
            <input placeholder='Software' onChange={handleChange}/>
            <button onClick={() => setStep(currStep => currStep + 1)} disabled={formData.software.length > 0 ? false : true}>Ok</button>
        </div>
    )
}

export default StepSoftware