import React , {useState} from 'react';
import StepRef from './StepRef';
import StepSoftware from './StepSoftware';
import StepEmpresa from './StepEmpresa';
import styles from "./Steps.module.css";
import ReactDOMServer from "react-dom/server";

const FormDisplay = ({step , formData , setFormData , setStep}) => {
    const title = ['Nombre de software' , 'Datos referente','Nombre empresa','Rubro']
    if(step === 0){
        return <StepSoftware setFormData={setFormData} setStep={setStep} formData={formData} title={title[step]}/>
    }else if(step === 1){
        return <StepRef setFormData={setFormData} setStep={setStep} formData={formData} title={title[step]}/>
    }else if(step === 2){
        return <StepEmpresa setFormData={setFormData} setStep={setStep} formData={formData} title={title[step]}/>
    }
}

const SignUp = () => {
    const [step , setStep] = useState(0)
    const [formData , setFormData] = useState({
        software:'',
        mail:'',
        referente:'',
        rubro:'',
        empresa:''
    })
    return(
        <div className={styles.container}>
            <FormDisplay step={step} formData={formData} setFormData={setFormData}  setStep={setStep}/>
        </div>
    )
}

export default SignUp;

const html = ReactDOMServer.renderToStaticMarkup(<SignUp />);
console.log(html)