import styles from "./Steps.module.css"

const StepRef = ({setFormData , setStep , formData , title}) => {
 // eslint-disable-next-line
    const regex = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    const handleChange = (e , formKey) => setFormData({...formData , [formKey]:e.target.value})

    return(
        <div className={styles.container}>
            <h2>{title}</h2>
            <input placeholder='Nombre referente' onChange={e => handleChange(e , 'referente')}/>
            <input placeholder='Mail referente' onChange={e => handleChange(e, 'mail')}/>
            <button 
                onClick={() => setStep(currStep => currStep + 1)} 
                disabled={formData.referente.length > 0 && regex.test(formData.mail) == true ? false : true}>Ok</button>
        </div>
    )
}

export default StepRef